import "./App.css";

import { createRootStore, StoreContext } from "./stores/RootStore";
import { DataProvider, useData } from "./components/DataContext";

import { CardFront } from "./components/CardFront";
import { CardBack } from "./components/CardBack";
import GBIcon from "./components/GBIcon";

const RootStore = createRootStore();

function App() {
  return (
    <StoreContext.Provider value={RootStore}>
      <DataProvider>
        <div className="App">
          <Main />
        </div>
      </DataProvider>
    </StoreContext.Provider>
  );
}

function Main(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div className="Main">
      <header>
        <GuildList />
        <ModelLists />
      </header>
      <div className="Cards">
        {data.Guilds.map((g) =>
          g.roster.map((m) => (
            <Content
              name={m}
              guild={g.name}
              id={`${g.name}-${m}`}
              key={`${g.name}-${m}`}
            />
          ))
        )}
      </div>
    </div>
  );
}

function SelectGuild(name) {
  document
    .querySelectorAll(".model-list")
    .forEach((m) => (m.style.display = "none"));
  document.querySelector(`.model-list#${name}`).style.display = "inline-block";
}

function GuildList(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        display: "grid",
        gap: "2px",
        // gridTemplateColumns: "repeat(4, 1fr)",
        gridTemplateRows: "repeat(7, 1fr)",
        gridAutoFlow: "column",
      }}
    >
      {data.Guilds.map((g) => (
        <div
          className="guild"
          key={g.name}
          onClick={() => {
            SelectGuild(g.name);
          }}
          style={{ "--guild-color": g.shadow ?? g.color }}
        >
          <span style={{ display: "inline-flex" }}>
            <div
              style={{
                backgroundColor: "black",
                width: "2em",
                height: "2em",
                borderRadius: "1em",
                display: "inline-flex",
                overflow: "visible",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <GBIcon
                icon={g.name}
                style={{
                  height: "2em",
                  fill: g.darkColor ?? g.color,
                  flexShrink: 0,
                }}
              />
            </div>
            <span
              style={{
                alignSelf: "center",
                marginLeft: "1em",
                marginRight: "1em",
              }}
            >
              {g.name}
            </span>
          </span>
        </div>
      ))}
    </div>
  );
}

function DisplayModel(guild, name) {
  var check = document.getElementById(guild + "-" + name + "_check");
  var card = document.querySelector(`.card#${guild}-${name}`);
  if (check.checked === true) {
    card.style.display = "inline-block";
  } else {
    card.style.display = "none";
  }
}

function ModelLists(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div className="model-list-container" style={{ width: "100%" }}>
      {data.Guilds.map((g) => (
        <div
          id={g.name}
          key={g.name}
          className="model-list"
          style={{
            // display: "inline-block",
            display: "none",
            // border: "1px solid white",
          }}
        >
          {g.roster.map((m) => (
            <label
              key={`${g.name}-${m}`}
              style={{
                display: "inline-block",
                border: "1px solid white",
                margin: "3px",
                padding: "0.5em",
                backgroundColor: g.shadow ?? g.color + "80",
                textShadow: "0.1em 0.1em 0.1em black",
              }}
            >
              <input
                type="checkbox"
                name={m}
                id={`${g.name}-${m}_check`}
                key={`${g.name}-${m}`}
                onChange={() => DisplayModel(g.name, m)}
              />
              {m}
            </label>
          ))}
        </div>
      ))}
    </div>
  );
}

function Content({ name, guild, id }) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  const model = data.Models.find((m) => m.id === name);
  return (
    <div
      className="card nobreak"
      // id={name}
      id={id}
      style={{
        display: "none",
        width: "5in",
        height: "3.5in",
        border: "1px solid black",
      }}
    >
      <CardFront
        model={model}
        guild={guild}
        style={{
          maxWidth: "2.5in",
          borderRadius: 0,
          lineHeight: "initial",
          "--scale": "calc(2.5 * 96 / 500)",
        }}
      />
      <CardBack
        model={model}
        guild={guild}
        style={{
          maxWidth: "2.5in",
          borderRadius: 0,
          lineHeight: "initial",
          "--scale": "calc(2.5 * 96 / 500)",
        }}
      />
    </div>
  );
}

export default App;
