import IcoMoon, { iconList } from "react-icomoon";
const iconSet = require("../assets/GuildBallIcons.json");
const pbIconSet = require("../assets/GBPlaybook.json");

const GBIconIco = (props) => <IcoMoon iconSet={iconSet} {...props} />;
const GBIcon = (props) => <GBIconIco {...props} removeInlineStyle={true} />;
export default GBIcon;

const PBIcon = (props) => <IcoMoon iconSet={pbIconSet} {...props} />;

const PB = ({ icon }) => {
  const [pb, mom] = icon ? icon.split(";") : [null, null];
  const i = pb.replace(",", "-").replace(/</g, "D").replace(/>/g, "P");
  return <PBIcon icon={i} size={44} removeInlineStyle={true} />;
};

export { PBIcon, PB };
