import React from "react";
import reactStringReplace from "react-string-replace";
import { PBIcon } from "./GBIcon";
export { textIconReplace };

function textIconReplace(text) {

  // TODO, FIXME, non-unique keys caused by replacements
  // return text;

  let replacedtext = reactStringReplace(
    text,
    /\((.*?)\)/g,
    (match, index, offset) => (
      <React.Fragment key={`i-a-${index}`}>
        (
        <span
          style={{
            fontStyle: "italic",
          }}
        >
          {match}
        </span>
        )
      </React.Fragment>
    )
  );

  replacedtext = reactStringReplace(
    replacedtext,
    /\(([^].*?)\)/g,
    (match, index, offset) => (
      <React.Fragment key={`i-${index}`}>
        (
        <span
          style={{
            fontStyle: "italic",
          }}
        >
          {match}
        </span>
        )
      </React.Fragment>
    )
  );

  replacedtext = reactStringReplace(
    replacedtext,
    /([<>TKDGB]+) playbook result/,
    (match, index, offset) => (
      <React.Fragment key={`pb-${index}`}>
        <span
          style={{
            display: "inline-flex",
            /* this should match the contained icon */
            width: "21px",
            /* this is matched to the x-hight of the text */
            height: "11px",
            overflow: "visible",
          }}
        >
          <div
            style={{
              display: "flex",
              overflow: "visible",
              width: "21px",
              height: "21px",
              backgroundColor: "white",
              border: "1px solid black",
              borderRadius: "21px",
              position: "absolute",
              alignSelf: "center",
              justifySelf: "center",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <PBIcon
              icon={match
                .replace(",", "-")
                .replace(/GB/g, "CP")
                .replace(/</g, "D")
                .replace(/>/g, "P")}
              size={21}
              color="black"
              removeInlineStyle={true}
            />
          </div>
        </span>
        {" playbook result"}
      </React.Fragment>
    )
  );

  // return <> {replacedtext}</>;

  replacedtext = reactStringReplace(
    replacedtext,
    /\b([A-Z]+)\b/g,
    (match, index, offset) => (
      <span
        key={`tla-${index}`}
        style={{
          letterSpacing: "-1px",
        }}
      >
        {match}
      </span>
    )
  );

  return <>{replacedtext}</>;
}
