import React, { useState, useRef, useLayoutEffect } from "react";
import GBImages from "../components/GBImages";
import { useData } from "./DataContext";

import GBIcon, { PB, PBIcon } from "../components/GBIcon";
import "./CardBack.css";

import useFitText from "use-fit-text";
import reactStringReplace from "react-string-replace";

import { textIconReplace } from "./CardUtils";

const CardBack = (props) => {
  const model = props.model;
  const key = model.id;
  const targetRef = useRef();
  const [scale, setScale] = useState(props.scale ?? 1.0);

  const { data } = useData();
  const guild = data.Guilds.find(
    (g) => g.name === (props.guild ?? model.guild1)
  );

  function updateSize() {
    let newScale = targetRef.current.getBoundingClientRect().width / 500;
    // let newScale = targetRef.current.getBoundingClientRect().height / 700;
    // console.log(`scale = ${newScale}`);
    setScale(newScale);
  }

  useLayoutEffect(() => {
    updateSize();
    window.addEventListener("resize", updateSize);
    return () => window.removeEventListener("resize", updateSize);
  });
  // }, [targetRef.current]);

  // console.log(guild);

  return (
    <div
      className={`card-back ${key} ${model.gbcp && "gbcp"}`}
      ref={targetRef}
      style={{
        "--gbcp": model.gbcp,
        "--scale": scale,
        "--team-color": guild.color,
        "--mom-color": guild.shadow,
        "--mom-border-color": guild.darkColor,
        backgroundImage: `url(${GBImages[key + "_back"]})`,
        ...props.style,
      }}
    >
      {model.gbcp || (
        <div className="overlay">
          <div className="container">
            <div className="name-plate">
              <div className="guild-icon">
                {/* <GBIcon icon={model.guild1} style={{ height: 45 }} /> */}
                <GBIcon icon={guild.name} style={{ height: 45 }} />
              </div>
              <div className="name">
                <div>{model.name}</div>
              </div>
            </div>
            <CharacterTraits model={model} />
            <Heroic model={model} />
            <Legendary model={model} />
          </div>
          <div className="footer">
            <div className="tags">{model.types}</div>
            <div className="right">
              <div className="icons">
                <FooterIcon icon="GB" />
                {model.guild2 && <FooterIcon icon={model.guild2} />}
                <FooterIcon icon={model.guild1} />
              </div>
              <div className="base-size">{`Size ${model.base} mm`}</div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const FooterIcon = ({ icon }) => (
  <div className="icon wrapper">
    <GBIcon
      icon="blank"
      fill="white"
      style={{ position: "absolute", zIndex: -1 }}
    />
    {/*
    <div
      style={{
        width: "38px",
        height: "38px",
        margin: "3px",
        borderRadius: "38px",
        backgroundColor: "white",
        position: "absolute",
        zIndex: -1,
      }}
    />
    */}
    <GBIcon icon={icon} />
  </div>
);

function CTName({ text }) {
  const name = text.split("[", 1)[0];
  const arg = text.replace(/[^[]*(\[.*\])?/, " $1");
  return (
    <div>
      <span>{name}</span>
      <span>{arg}</span>
    </div>
  );
}

const CharacterTraits = ({ model }) => {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  const Traits = data["Character Traits"];

  return (
    <div className="character-traits">
      <div className="header">Character Traits</div>
      {model.character_traits.map((key, index) => {
        const ct = Traits.find((ct) => ct.name === key.replace(/ \[.*\]/, ""));
        return (
          <div className="character-trait" key={`${key}-${index}`}>
            <div className={`trait ${ct.active && "active"}`}>
              <CTName text={key} />
            </div>
            <div className="text">{textIconReplace(ct.text)}</div>
            {/* <div className="text">{ct.text}</div> */}
          </div>
        );
      })}
    </div>
  );
};

const Heroic = ({ model }) => {
  if (!model.heroic) {
    return null;
  }
  const name = model.legendary.split("\n", 1)[0];
  const text = model.legendary.split("\n").slice(1).join("\n");
  return (
    <>
      <div className="header">Heroic Play</div>
      <div className="heroic">
        <CTName text={name} />
        <div>{textIconReplace(text)}</div>
      </div>
    </>
  );
};

const Legendary = ({ model }) => {
  if (!model.legendary) {
    return null;
  }
  const name = model.legendary.split("\n", 1)[0];
  const text = model.legendary.split("\n").slice(1).join("\n");
  return (
    <>
      <div className="header">Legendary Play</div>
      <div className="legendary">
        <CTName text={name} />
        <div>{textIconReplace(text)}</div>
      </div>
    </>
  );
};

export { CardBack };
